// ==========================================
// RISIVO CRM - Power Dialer Queue API
// ==========================================

import { NextRequest, NextResponse } from 'next/server'
import { supabaseAdmin } from '@/lib/supabase/server'
import { DialerQueueItem, QueueItemStatus, DialerApiResponse, PaginatedResponse } from '@/lib/power-dialer/types'

// Initialize Supabase client
const supabase = supabaseAdmin

// GET - List queue items or get next item to dial
export async function GET(request: NextRequest) {
  try {
    const { searchParams } = new URL(request.url)
    const subAccountId = searchParams.get('subAccountId')
    const campaignId = searchParams.get('campaignId')
    const queueItemId = searchParams.get('id')
    const status = searchParams.get('status') as QueueItemStatus | null
    const getNext = searchParams.get('getNext') === 'true'
    const agentId = searchParams.get('agentId')
    const page = parseInt(searchParams.get('page') || '1')
    const pageSize = parseInt(searchParams.get('pageSize') || '50')
    
    if (!subAccountId) {
      return NextResponse.json({
        success: false,
        error: 'subAccountId is required'
      }, { status: 400 })
    }
    
    // Get single queue item
    if (queueItemId) {
      const { data: item, error } = await supabase
        .from('DialerQueue')
        .select('*')
        .eq('id', queueItemId)
        .eq('subAccountId', subAccountId)
        .single()
      
      if (error) {
        return NextResponse.json({
          success: false,
          error: error.message
        }, { status: 404 })
      }
      
      return NextResponse.json({
        success: true,
        data: item
      })
    }
    
    // Get next item to dial (for auto-dialer)
    if (getNext && campaignId) {
      const now = new Date().toISOString()
      
      // Find next available item that:
      // 1. Is in pending status
      // 2. Is not locked by another agent
      // 3. nextAttemptAfter is null or in the past
      // 4. Has attempts < maxAttempts
      const { data: nextItem, error } = await supabase
        .from('DialerQueue')
        .select('*')
        .eq('subAccountId', subAccountId)
        .eq('campaignId', campaignId)
        .eq('status', 'pending')
        .or(`lockedBy.is.null,lockedAt.lt.${new Date(Date.now() - 5 * 60 * 1000).toISOString()}`) // Unlock after 5 min
        .or(`nextAttemptAfter.is.null,nextAttemptAfter.lte.${now}`)
        .order('priority', { ascending: false })
        .order('position', { ascending: true })
        .limit(1)
        .single()
      
      if (error || !nextItem) {
        return NextResponse.json({
          success: true,
          data: null,
          message: 'No items available in queue'
        })
      }
      
      // Lock the item for this agent
      if (agentId) {
        await supabase
          .from('DialerQueue')
          .update({
            lockedBy: agentId,
            lockedAt: now,
            status: 'in_progress'
          })
          .eq('id', nextItem.id)
      }
      
      return NextResponse.json({
        success: true,
        data: nextItem
      })
    }
    
    // List queue items
    let query = supabase
      .from('DialerQueue')
      .select('*', { count: 'exact' })
      .eq('subAccountId', subAccountId)
      .order('priority', { ascending: false })
      .order('position', { ascending: true })
    
    if (campaignId) {
      query = query.eq('campaignId', campaignId)
    }
    
    if (status) {
      query = query.eq('status', status)
    }
    
    // Pagination
    const from = (page - 1) * pageSize
    const to = from + pageSize - 1
    query = query.range(from, to)
    
    const { data: items, error, count } = await query
    
    if (error) {
      return NextResponse.json({
        success: false,
        error: error.message
      }, { status: 500 })
    }
    
    const response: PaginatedResponse<DialerQueueItem> = {
      success: true,
      data: items || [],
      pagination: {
        page,
        pageSize,
        total: count || 0,
        totalPages: Math.ceil((count || 0) / pageSize)
      }
    }
    
    return NextResponse.json(response)
    
  } catch (error) {
    console.error('GET /api/v1/dialer/queue error:', error)
    return NextResponse.json({
      success: false,
      error: 'Internal server error'
    }, { status: 500 })
  }
}

// POST - Add contacts to queue (bulk add)
export async function POST(request: NextRequest) {
  try {
    const body = await request.json()
    const { subAccountId, campaignId, contacts } = body
    
    if (!subAccountId || !campaignId) {
      return NextResponse.json({
        success: false,
        error: 'subAccountId and campaignId are required'
      }, { status: 400 })
    }
    
    if (!contacts || !Array.isArray(contacts) || contacts.length === 0) {
      return NextResponse.json({
        success: false,
        error: 'contacts array is required'
      }, { status: 400 })
    }
    
    // Get current max position in queue
    const { data: maxPosResult } = await supabase
      .from('DialerQueue')
      .select('position')
      .eq('campaignId', campaignId)
      .order('position', { ascending: false })
      .limit(1)
      .single()
    
    let currentPosition = maxPosResult?.position || 0
    
    // Check DNC list
    const phoneNumbers = contacts.map((c: { phone: string }) => c.phone).filter(Boolean)
    const { data: dncList } = await supabase
      .from('DialerDNC')
      .select('normalizedNumber')
      .eq('subAccountId', subAccountId)
      .in('normalizedNumber', phoneNumbers)
    
    const dncNumbers = new Set(dncList?.map(d => d.normalizedNumber) || [])
    
    // Prepare queue items
    const queueItems = contacts
      .filter((contact: { phone: string }) => contact.phone && !dncNumbers.has(contact.phone))
      .map((contact: { id: string; phone: string; firstName?: string; lastName?: string; email?: string; priority?: number }) => ({
        subAccountId,
        campaignId,
        contactId: contact.id,
        phoneNumber: contact.phone,
        contactName: contact.firstName && contact.lastName 
          ? `${contact.firstName} ${contact.lastName}` 
          : contact.firstName || contact.lastName || null,
        contactEmail: contact.email || null,
        contactData: contact,
        status: 'pending',
        priority: contact.priority || 50,
        position: ++currentPosition,
        attempts: 0,
        maxAttempts: 3
      }))
    
    if (queueItems.length === 0) {
      return NextResponse.json({
        success: false,
        error: 'No valid contacts to add (all may be on DNC list or missing phone numbers)'
      }, { status: 400 })
    }
    
    // Insert queue items
    const { data: insertedItems, error } = await supabase
      .from('DialerQueue')
      .insert(queueItems)
      .select()
    
    if (error) {
      return NextResponse.json({
        success: false,
        error: error.message
      }, { status: 500 })
    }
    
    // Update campaign contact counts
    const { count: totalInQueue } = await supabase
      .from('DialerQueue')
      .select('*', { count: 'exact', head: true })
      .eq('campaignId', campaignId)
    
    const { count: pendingInQueue } = await supabase
      .from('DialerQueue')
      .select('*', { count: 'exact', head: true })
      .eq('campaignId', campaignId)
      .eq('status', 'pending')
    
    await supabase
      .from('DialerCampaign')
      .update({
        totalContacts: totalInQueue || 0,
        contactsRemaining: pendingInQueue || 0,
        updatedAt: new Date().toISOString()
      })
      .eq('id', campaignId)
    
    return NextResponse.json({
      success: true,
      data: {
        added: insertedItems?.length || 0,
        skippedDNC: contacts.length - queueItems.length
      },
      message: `Added ${insertedItems?.length} contacts to queue`
    }, { status: 201 })
    
  } catch (error) {
    console.error('POST /api/v1/dialer/queue error:', error)
    return NextResponse.json({
      success: false,
      error: 'Internal server error'
    }, { status: 500 })
  }
}

// PUT - Update queue item (status, priority, scheduling)
export async function PUT(request: NextRequest) {
  try {
    const body = await request.json()
    const { id, subAccountId, ...updateData } = body
    
    if (!id || !subAccountId) {
      return NextResponse.json({
        success: false,
        error: 'id and subAccountId are required'
      }, { status: 400 })
    }
    
    // Update queue item
    const { data: item, error } = await supabase
      .from('DialerQueue')
      .update({
        ...updateData,
        updatedAt: new Date().toISOString()
      })
      .eq('id', id)
      .eq('subAccountId', subAccountId)
      .select()
      .single()
    
    if (error) {
      return NextResponse.json({
        success: false,
        error: error.message
      }, { status: 500 })
    }
    
    return NextResponse.json({
      success: true,
      data: item,
      message: 'Queue item updated successfully'
    })
    
  } catch (error) {
    console.error('PUT /api/v1/dialer/queue error:', error)
    return NextResponse.json({
      success: false,
      error: 'Internal server error'
    }, { status: 500 })
  }
}

// DELETE - Remove item from queue
export async function DELETE(request: NextRequest) {
  try {
    const { searchParams } = new URL(request.url)
    const id = searchParams.get('id')
    const subAccountId = searchParams.get('subAccountId')
    const campaignId = searchParams.get('campaignId')
    const clearAll = searchParams.get('clearAll') === 'true'
    
    if (!subAccountId) {
      return NextResponse.json({
        success: false,
        error: 'subAccountId is required'
      }, { status: 400 })
    }
    
    // Clear entire queue for a campaign
    if (clearAll && campaignId) {
      const { error } = await supabase
        .from('DialerQueue')
        .delete()
        .eq('subAccountId', subAccountId)
        .eq('campaignId', campaignId)
        .eq('status', 'pending')
      
      if (error) {
        return NextResponse.json({
          success: false,
          error: error.message
        }, { status: 500 })
      }
      
      // Update campaign counts
      await supabase
        .from('DialerCampaign')
        .update({
          contactsRemaining: 0,
          updatedAt: new Date().toISOString()
        })
        .eq('id', campaignId)
      
      return NextResponse.json({
        success: true,
        message: 'Queue cleared successfully'
      })
    }
    
    // Delete single item
    if (!id) {
      return NextResponse.json({
        success: false,
        error: 'id is required'
      }, { status: 400 })
    }
    
    const { error } = await supabase
      .from('DialerQueue')
      .delete()
      .eq('id', id)
      .eq('subAccountId', subAccountId)
    
    if (error) {
      return NextResponse.json({
        success: false,
        error: error.message
      }, { status: 500 })
    }
    
    return NextResponse.json({
      success: true,
      message: 'Queue item deleted successfully'
    })
    
  } catch (error) {
    console.error('DELETE /api/v1/dialer/queue error:', error)
    return NextResponse.json({
      success: false,
      error: 'Internal server error'
    }, { status: 500 })
  }
}
